<template>
     <v-timeline dense>
          <v-timeline-item 
               v-for="(position, i) in positions"
               :key="i"
               :large="true"
               class="timeline_item"
          >
               <v-card>
                    <v-card-title class="title row">
                         <span class="position_title text-uppercase word_break col-sm-6">{{position.title}}</span>
                         <span class="position_company col-sm-6" style="text-align:right">
                              <a 
                                   v-if="position.website" 
                                   :href="position.website"
                                   target="_blank"
                                   style="color:rgb(57, 71, 204);"
                                   class="word_break"
                              >
                                   {{position.company}}
                              </a>
                              <span class="word_break" v-else>{{position.company}}</span>
                              <p class="position_date word_break">
                                   {{position.dateFrom}} - {{position.dateTo}} ({{getDuration(position.dateFrom, position.dateTo)}})
                              </p>
                         </span>
                    </v-card-title>
               </v-card>
          </v-timeline-item>
     </v-timeline>
</template>

<script>
export default {
     name: 'Experience',
     data: () => ({
          positions: [
               {
                    title:'Full Stack Software Engineer',
                    company:'Washmen',
                    website:'http://www.washmen.com/',
                    dateFrom:'Sep 2020',
                    dateTo:'Present',
               },
               {
                    title:'Full Stack Software Engineer',
                    company:'Tecfrac',
                    website:'http://www.tecfrac.com/',
                    dateFrom:'Nov 2019',
                    dateTo:'Sep 2020',
               },
               {
                    title:'Undergraduate Research Assistant',
                    company:'Lebanese American University',
                    website:'https://www.lau.edu.lb/',
                    dateFrom:'Sep 2018',
                    dateTo:'May 2019',
               },
               {
                    title:'Full Stack Software Engineer',
                    company:'Safa Group for Marble & Granite',
                    website:'http://safa-group.com/',
                    dateFrom:'Jan 2019',
                    dateTo:'April 2019',
               },
               {
                    title:'Freelance Software Engineer',
                    company:'Self-employed',
                    website:'',
                    dateFrom:'Sep 2017',
                    dateTo:'Jan 2019',
               },
               {
                    title:'Software Engineering Intern',
                    company:'Techpunto',
                    website:'http://techpunto.com/',
                    dateFrom:'Jun 2018',
                    dateTo:'Jul 2018',
               },
               {
                    title:'Sales Assistant',
                    company:'BE Home Appliances',
                    website:'',
                    dateFrom:'2013',
                    dateTo:'Sep 2016',
               }
          ]
     }),
     methods:{
          getDuration(from, to) {
               from = new Date(from);
               to = (to == "Present") ? (new Date()).setDate(16) : new Date(to);
               var diff = to - from; 
               diff /= 1000;
               diff /= 60;
               diff /= 60;
               diff /= 24;
               diff /= 30;
               diff = Math.ceil(diff)
               var years = 0, months = 0;
               while(diff >= 12) {
                    years++;
                    diff -= 12;
               }
               months = diff;
               var yearsText = "", monthsText = "";
               if(years > 0) {
                    if(years > 1) yearsText = years + " yrs";
                    else yearsText = years + " yr";
               }
               if(months > 0) {
                    if(months > 1) monthsText = months + " mos";
                    else monthsText = months + " mo";
               }
               var res = "";
               if(yearsText && monthsText) res = yearsText + " " + monthsText;
               else res = yearsText + monthsText;
               return res;
          }
     }
};
</script>

<style scoped>
.position_description{
     padding-left:15px;
}
.position_date{
     font-size: 17px;
     font-style: italic;
}
.position_title{
     font-weight: bold;
}
.position_company{
     font-family: 'Rockwell';
     color:rgb(57, 71, 204);
}
.timeline_item{
     margin-right:20px;
}
.word_break{
     width:100%;
     word-break: normal;
}
</style>